import { useState } from 'react';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import LoginRoundedIcon from '@mui/icons-material/LoginRounded';
import { fetchUserData } from '../../authApi';
import ReCAPTCHA from 'react-google-recaptcha';
import axios from 'axios';

const Login = ( { setUser }) => {
    const [data, setData] = useState({username: '', password: ''})

    // Form Submit
    const handleSubmit = async (e) => {
        e.preventDefault()
        const response = await axios.post(`${process.env.REACT_APP_SERVER_URL}users/login/`, data, {headers: {'Content-Type': 'application/json'}});
    
        if (response.status === 200) {
            const { access, refresh } = response.data;
            
            // Store tokens in localStorage
            localStorage.setItem('accessToken', access);
            localStorage.setItem('refreshToken', refresh);
            
            const user = await fetchUserData();
            if (user) {
                setUser(user);
            }
        } else {
            throw new Error(response.statusText);
        }
    }

    return (
        <Container maxWidth="md">
            <Typography variant="h2" gutterBottom sx={{textAlign: { xs: 'center', md: 'left' }}}>Login</Typography>
            <Paper
            component="form"
            noValidate
            autoComplete="off"
            onSubmit={handleSubmit}
            sx={{p: 4}}
            >
                <TextField id="username" label="Username" variant="outlined" value={data.username} onChange={(e) => setData({...data, username: e.target.value})} fullWidth sx={{mb:4}} />
                <TextField id="password" label="Password" variant="outlined" value={data.password} onChange={(e) => setData({...data, password: e.target.value})} fullWidth type='password' sx={{mb:4}} />
                <ReCAPTCHA sitekey={process.env.REACT_APP_RECAPTCHA_KEY} keyType="v2" />
                <Button variant="contained" type='submit' size="large" fullWidth sx={{mt:4}}>Login&nbsp;<LoginRoundedIcon /></Button>
            </Paper>
        </Container>
    )
}

export default Login