import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Link } from 'react-router-dom';

const QuoteList = ( { quotes }) => {
  return (
    <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
            <TableRow>
            <TableCell>Business Name</TableCell>
            <TableCell align="right">Contact Name</TableCell>
            <TableCell align="right">Date</TableCell>
            <TableCell align="right">Type</TableCell>
            <TableCell align="right">Amount</TableCell>
            <TableCell align="right">Quote</TableCell>
            </TableRow>
        </TableHead>
        <TableBody>
            {quotes.map((quote) => (
            <TableRow
                key={quote.id}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
                <TableCell component="th" scope="quote">
                {quote.business_name}
                </TableCell>
                <TableCell align="right">{quote.contact_name}</TableCell>
                <TableCell align="right">{quote.date}</TableCell>
                <TableCell align="right">{quote.brazed ? "Brazed" : "Electoplated"}</TableCell>
                <TableCell align="right">${quote.price}</TableCell>
                <TableCell align="right"><Link to={`/quotes/${quote.id}`}>View</Link></TableCell>
            </TableRow>
            ))}
        </TableBody>
        </Table>
    </TableContainer>
  )
}

export default QuoteList