import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Link, useNavigate } from 'react-router-dom';
import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Fab from '@mui/material/Fab';
import Typography from '@mui/material/Typography';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import EditRoundedIcon from '@mui/icons-material/EditRounded';
import InfoRoundedIcon from '@mui/icons-material/InfoRounded';
import OilBarrelRoundedIcon from '@mui/icons-material/OilBarrelRounded';
import DiamondRoundedIcon from '@mui/icons-material/DiamondRounded';
import EngineeringRoundedIcon from '@mui/icons-material/EngineeringRounded';
import ConstructionRoundedIcon from '@mui/icons-material/ConstructionRounded';
import GroupsRoundedIcon from '@mui/icons-material/GroupsRounded';
import AccountTreeRoundedIcon from '@mui/icons-material/AccountTreeRounded';
import axios from 'axios';

const QuoteView = ( { refreshAccessToken } ) => {
  const navigate = useNavigate();
  const { quote_id } = useParams();
  const [quote, setQuote] = useState({business_name: '', contact_name: '', notes: '', brazed: false, surface_area: 0, electroless: false})
  const {business_name, contact_name, notes, brazed, surface_area, electroless, particle, grit, fixture, fixture_cost, tooling_type, strip_replate, blank_cost, machining, machine_cost, prep_labor, plating_labor, cleaning_labor, pack_labor, difficulty, expedited, price} = quote

  const handleDelete = async () => {
    const accessToken = localStorage.getItem('accessToken');
    if(accessToken) {
      try {
        const response = await axios.delete(`${process.env.REACT_APP_SERVER_URL}quotes/${quote.id}`, {headers: { Authorization: `Bearer ${accessToken}` }});
        
        if (response.status === 204) {
          navigate('/quotes')
        }
      } catch (error) {
        if (error.response && error.response.status === 401) {
          console.log(error.response)
        }
        console.error(error);
      }
  }
  }

  useEffect(() => {
    const getQuote = async () => {
      const accessToken = localStorage.getItem('accessToken');
  
      if (accessToken) {
        try {
          const response = await axios.get(`${process.env.REACT_APP_SERVER_URL}quotes/${quote_id}`, {
            headers: { Authorization: `Bearer ${accessToken}` },
          });
  
          if (response.status === 200) {
            setQuote(response.data);
          }
        } catch (error) {
          if (error.response && error.response.status === 401) {
            await refreshAccessToken();
            return getQuote();
          }
          console.error(error);
        }
      }
    };
  
    getQuote();
  }, []);


  return (
    <Container maxWidth="lg">
      <Box sx={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2}}>
        <Typography variant="h4" gutterBottom sx={{mb:0, textAlign: {xs: 'center', md: 'left'}, fontSize: { xs: '1.2rem', md: '2.2rem' }}}>{quote.business_name} - {quote.date}</Typography>
        <Stack direction='horizontal' gap={2}>
          <Link to={`/quotes/edit/${quote.id}`}><Fab color="primary" aria-label="edit"><EditRoundedIcon /></Fab></Link>
          <Fab color="error" aria-label="delete" onClick={handleDelete}><DeleteRoundedIcon /></Fab>
        </Stack>
      </Box>
      <Stack gap={5}>
        {/* Info */}
        <Paper elevation={2} sx={{p: 2}}>
        <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><InfoRoundedIcon sx={{mr: 1}} /><span>Info</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Business Name:</strong> {business_name}</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Contact:</strong> {contact_name}</Typography>
          {notes && <Typography variant="subtitle1" gutterBottom><strong>Notes:</strong> {notes}</Typography>}
        </Paper>

        {/* Plating */}
        <Paper elevation={2} sx={{p: 2}}>
        <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><OilBarrelRoundedIcon sx={{mr: 1}} /><span>Plating</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Type:</strong> {brazed ? "Brazed" : "Electroplated"}</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Surface Area:</strong> {surface_area} sq. inches</Typography>
          {electroless && <Typography variant="subtitle1" gutterBottom><strong>Electroless:</strong> Yes</Typography>}
        </Paper>

        {/* Particles */}
        <Paper elevation={2} sx={{p: 2}}>
        <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><DiamondRoundedIcon sx={{mr: 1}} /><span>Particles</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Type:</strong> {particle}</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Grit Size:</strong> {grit}</Typography>
        </Paper>

        {/* Fixture */}
        <Paper elevation={2} sx={{p: 2}}>
          <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><EngineeringRoundedIcon sx={{mr: 1}} /><span>Fixture</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Fixture:</strong> {fixture ? 'Yes' : 'No'}</Typography>
          {fixture ? <Typography variant="subtitle1" gutterBottom><strong>Fixture Cost:</strong> ${fixture_cost}</Typography> : null}
        </Paper>

        {/* Tooling */}
        <Paper elevation={2} sx={{p: 2}}>
          <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><ConstructionRoundedIcon sx={{mr: 1}} /><span>Tooling</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Type:</strong> {tooling_type}</Typography>
          {tooling_type === "Strip & Replate" && <Typography variant="subtitle1" gutterBottom><strong>Difficulty:</strong> {strip_replate}</Typography>}
          {tooling_type === "Blank Needed" && <Typography variant="subtitle1" gutterBottom><strong>Blank Cost:</strong> ${blank_cost}</Typography>}
          {machining && <Typography variant="subtitle1" gutterBottom><strong>Additional Machining:</strong> Yes</Typography>}
          {machining && <Typography variant="subtitle1" gutterBottom><strong>Machine Cost:</strong> ${machine_cost}</Typography>}
        </Paper>

        {/* Labor */}
        <Paper elevation={2} sx={{p: 2}}>
          <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><GroupsRoundedIcon sx={{mr: 1}} /><span>Labor</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Prep Time:</strong> {prep_labor} hrs.</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Plating Time:</strong> {plating_labor} hrs.</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Cleanup Time:</strong> {cleaning_labor} hrs.</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Packing Time:</strong> {pack_labor} hrs.</Typography>
        </Paper>

        {/* Project */}
        <Paper elevation={2} sx={{p: 2}}>
          <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><AccountTreeRoundedIcon sx={{mr: 1}} /><span>Project</span></div></Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Project Difficulty:</strong> {difficulty}</Typography>
          <Typography variant="subtitle1" gutterBottom><strong>Need to be Expedited:</strong> {expedited ? 'Yes': 'No'}</Typography>
        </Paper>

        {/* Price */}
        <Paper elevation={2} sx={{py: 2, px: 4, width: 'fit-content'}}>
          <Typography variant="h5" gutterBottom sx={{fontWeight: 'bold'}}>Total: ${price}</Typography>
        </Paper>
      </Stack>
    </Container>
  )
}

export default QuoteView