import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import AddCircleRoundedIcon from '@mui/icons-material/AddCircleRounded';
import QuoteList from '../QuoteList';
import axios from 'axios';

const Quotes = () => {
    const navigate = useNavigate()
    const [quotes, setQuotes] = useState([])

    useEffect(() => {
        const getQuotes = async () => {
            const accessToken = localStorage.getItem('accessToken')
            if(accessToken) {
                const response = await axios.get(`${process.env.REACT_APP_SERVER_URL}quotes`,
                {headers: {"Authorization": `Bearer ${accessToken}`}});
              
                if (response.status === 200) {
                  setQuotes(response.data)
                } else {
                  throw new Error(response.statusText);
                }
            }
        };

        getQuotes()
    }, [])

    return (
        <Container maxWidth="lg">
            <Typography variant="h2" gutterBottom sx={{textAlign: { xs: 'center', md: 'left' }}}>Quotes</Typography>
            {quotes.length > 0 ? <QuoteList quotes={quotes} /> :
            <Alert variant="filled" severity="error" sx={{maxWidth: '600px', m: 'auto'}} action={
                <Button color="inherit" size="small" onClick={() => navigate('/')}>Create <AddCircleRoundedIcon sx={{ml: 1}} /></Button>
              }>No Quotes have been created yet...</Alert>
            }
        </Container>
    )
}

export default Quotes