import { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { ThemeProvider} from '@mui/material/styles';
import BrandBar from './components/BrandBar';
import Login from "./components/pages/Login";
import CreateQuote from "./components/pages/CreateQuote";
import Quotes from "./components/pages/Quotes";
import QuoteView from './components/pages/QuoteView';
import EditView from './components/pages/EditView';
import Box from '@mui/material/Box';
import { fetchUserData } from './authApi';
import { theme } from './Theme'
import axios from 'axios';

function App() {
  const [user, setUser] = useState(null)

  // Function to refresh the access token
  const refreshAccessToken = async () => {
    const refreshToken = localStorage.getItem('refreshToken');
    if (refreshToken) {
      try {
        const response = await axios.post(`${process.env.REACT_APP_SERVER_URL}login/refresh/`, {
          refresh: refreshToken
        });
        const newAccessToken = response.data.access;
        localStorage.setItem('accessToken', newAccessToken);
      } catch (err) {
        console.log(err)
      }
    }
  };

  useEffect(() => {
    async function getUserData() {
      const data = await fetchUserData();
      if (data) {
        setUser(data);
      }
    }

    getUserData();
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <Box backgroundColor={theme.palette.primary.light} sx={{width: '100%', minHeight: '100vh', display: 'flex', flexDirection: 'column'}}>
        <Router>
          <BrandBar user={user} setUser={setUser} />
          <Box sx={{py: 4, flexGrow: 1}}>
            {!user ? <Login setUser={setUser} /> :
              <Routes>
                <Route path="/" element={<CreateQuote refreshAccessToken={refreshAccessToken} />} />
                <Route path="/quotes" element={<Quotes refreshAccessToken={refreshAccessToken} />} />
                <Route path="/quotes/:quote_id" element={<QuoteView refreshAccessToken={refreshAccessToken} />} />
                <Route path="/quotes/edit/:quote_id" element={<EditView refreshAccessToken={refreshAccessToken} />} />
              </Routes>
            }
          </Box>
        </Router>`
      </Box>
    </ThemeProvider>
  );
}

export default App;
