import { useState } from 'react';
import { useNavigate } from "react-router-dom";
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import DiamondIcon from '@mui/icons-material/Diamond';


const BrandBar = ( { user, setUser }) => {
    const navigate = useNavigate()
    const [anchorElNav, setAnchorElNav] = useState(null);
    const [anchorElUser, setAnchorElUser] = useState(null);
  
    const handleOpenNavMenu = (event) => {
      setAnchorElNav(event.currentTarget);
    };
    const handleOpenUserMenu = (event) => {
      setAnchorElUser(event.currentTarget);
    };
  
    const handleCloseNavMenu = () => {
      setAnchorElNav(null);
    };
  
    const handleCloseUserMenu = () => {
      setAnchorElUser(null);
    };

    const handleLogout = () => {
        localStorage.removeItem("refreshToken")
        localStorage.removeItem("accessToken")
        setUser(null)
    }

    return (
        <AppBar position="static">
            <Container maxWidth="xl">
            <Toolbar disableGutters>
                <DiamondIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
                <Typography
                variant="h5"
                noWrap
                component="a"
                href="/"
                sx={{
                    mr: 2,
                    display: { xs: 'none', md: 'flex' },
                    fontFamily: 'roboto',
                    fontWeight: 700,
                    letterSpacing: '.15rem',
                    color: 'inherit',
                    textDecoration: 'none',
                }}
                >
                Niabraze
                </Typography>

                <Box sx={{ display: { xs: 'inline-block', md: 'none' }, position: 'fixed' }}>
                    <IconButton
                        size="large"
                        aria-label="account of current user"
                        aria-controls="menu-appbar"
                        aria-haspopup="true"
                        onClick={handleOpenNavMenu}
                        color="inherit"
                    >
                        <MenuIcon />
                    </IconButton>
                    <Menu
                        id="menu-appbar"
                        anchorEl={anchorElNav}
                        anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                        }}
                        keepMounted
                        transformOrigin={{
                        vertical: 'top',
                        horizontal: 'left',
                        }}
                        open={Boolean(anchorElNav)}
                        onClose={handleCloseNavMenu}
                        sx={{
                        display: { xs: 'block', md: 'none' },
                        }}
                    >
                        <MenuItem onClick={() => navigate('/')}><Typography textAlign="center">Create Quote</Typography></MenuItem>
                        <MenuItem onClick={() => navigate('/quotes')}><Typography textAlign="center">Quotes</Typography></MenuItem>
                        {user ?
                        <MenuItem><Typography textAlign="center" onClick={handleLogout}>Logout</Typography></MenuItem> :
                        <MenuItem onClick={() => navigate('/login')}><Typography textAlign="center">Login</Typography></MenuItem>
                        }
                    </Menu>
                </Box>
                <Box sx={{ display: { xs: 'flex', md: 'none' }, flexGrow: 1, justifyContent: 'center', alignItems: 'center'}}>
                    <DiamondIcon />
                    <Typography
                        variant="h4"
                        noWrap
                        component="a"
                        href="/"
                        sx={{
                            mr: 2,
                            ml: 1,
                            fontFamily: 'roboto',
                            fontWeight: 700,
                            color: 'inherit',
                            textDecoration: 'none',
                        }}
                    >
                    Niabraze
                    </Typography>
                </Box>
                <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                    <Button onClick={() => navigate('/')} sx={{ my: 2, color: 'white', display: 'block' }}>Create Quote</Button>
                    <Button onClick={() => navigate('/quotes')} sx={{ my: 2, color: 'white', display: 'block' }}>Quotes</Button>
                </Box>

                <Box sx={{ flexGrow: 0, display: { xs: 'none', md: 'flex' }}}>
                    {user ?
                        <>
                            <Tooltip title="Open settings">
                                <Button onClick={handleOpenUserMenu} sx={{ p: 0, color: '#fff' }}>Hello {user.username}!</Button>
                            </Tooltip>
                            <Menu
                                sx={{ mt: '45px' }}
                                id="menu-appbar"
                                anchorEl={anchorElUser}
                                anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                                keepMounted
                                transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                                open={Boolean(anchorElUser)}
                                onClose={handleCloseUserMenu}
                            >
                                <MenuItem onClick={handleCloseUserMenu}>
                                    <Typography textAlign="center">Profile</Typography>
                                </MenuItem>
                                <MenuItem onClick={handleLogout}>
                                    <Typography textAlign="center">Logout</Typography>
                                </MenuItem>
                            </Menu>
                        </>
                        :
                        <Button onClick={() => navigate('/login')} sx={{ p: 0, color: '#fff' }}>Login</Button>

                    }
                </Box>
            </Toolbar>
            </Container>
        </AppBar>
    )
}

export default BrandBar