import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import Switch from '@mui/material/Switch';
import Select from '@mui/material/Select';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputAdornment from '@mui/material/InputAdornment';
import MenuItem from '@mui/material/MenuItem';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import InfoRoundedIcon from '@mui/icons-material/InfoRounded';
import OilBarrelRoundedIcon from '@mui/icons-material/OilBarrelRounded';
import DiamondRoundedIcon from '@mui/icons-material/DiamondRounded';
import EngineeringRoundedIcon from '@mui/icons-material/EngineeringRounded';
import ConstructionRoundedIcon from '@mui/icons-material/ConstructionRounded';
import GroupsRoundedIcon from '@mui/icons-material/GroupsRounded';
import AccountTreeRoundedIcon from '@mui/icons-material/AccountTreeRounded';
import SaveAsRoundedIcon from '@mui/icons-material/SaveAsRounded';
import AttachMoneyRoundedIcon from '@mui/icons-material/AttachMoneyRounded';
import axios from 'axios';


const particleSizes = {
    '16/18': .046,
    '18/20': .039,
    '20/30': .033,
    '30/40': .023,
    '40/50': .017,
    '50/60': .012,
    '60/80': .0098,
    '80/100': .007,
    '80/120': .0064,
    '100/120': .006,
    '120/140': .005,
    '140/170': .004,
    '170/200': .0035,
    '200/230': .003,
    '230/270': .0025,
    '270/325': .002,
    '325/400': .0018,
    '400': .0018,
    '500': .0012,
    '600': .00049,
    '800': .0003,
    '1200': .0002,
    '1500': .00018,
    '2000': .00011
}

const particlePerCarat = {
    '16/18': 33,
    '18/20': 57,
    '20/30': 282,
    '30/40': 282,
    '40/50': 770,
    '50/60': 2000,
    '60/80': 3000,
    '80/100': 10000,
    '80/120': 14000,
    '100/120': 17000,
    '120/140': 21000,
    '140/170': 49000,
    '170/200': 83000,
    '200/230': 140000,
    '230/270': 252000,
    '270/325': 284000,
    '325/400': 550000,
    '400': 550000,
    '500': 550000,
    '600': 1700000,
    '800': 7000000,
    '1200': 262000000,
    '1500': 262000000,
    '2000': 2050000000
}

const particleCosts = {
    synthetic: 0.25,
    natural: 0.8,
    blend: 0.42,
    cbn: 0.75
}

const laborRates = {
    easy: 60,
    moreDifficult: 70,
    veryDifficult: 80
}

const CreateQuote = ( { refreshAccessToken } ) => {
    const navigate = useNavigate()
    const [viewPrice, setPrice] = useState(0)
    const [quote, setQuote] = useState({
        business_name: '',
        contact_name: '',
        notes: '',
        brazed: false,
        surface_area: 0,
        electroless: false,
        particle: 'synthetic',
        grit: '',
        fixture: false,
        fixture_cost: 0,
        tooling_type: 'Customer Provided',
        strip_replate: 'small/easy part',
        blank_cost: 0,
        machining: false,
        machine_cost: 0,
        prep_labor: 0,
        plating_labor: 0,
        cleaning_labor: 0,
        pack_labor: 0,
        difficulty: 'easy',
        expedited: false,
        price: 0
    })

    // Calculate Price
    useEffect(() => {
        const costOfDiamond = ((quote.surface_area/particleSizes[quote.grit])/particlePerCarat[quote.grit]) * particleCosts[quote.particle]
        const labor = (Number(quote.prep_labor) + Number(quote.plating_labor) + Number(quote.cleaning_labor) + Number(quote.pack_labor)) * laborRates[quote.difficulty]
        const brazed = quote.brazed ? 1.2 : 1
        const expedited = quote.expedited ? 1.2 : 1
        const cost = (((labor + costOfDiamond + Number(quote.fixture_cost) + Number(quote.blank_cost) + Number(quote.machine_cost)) * brazed) * 1.2) * expedited
        setPrice(cost.toFixed(2))
    }, [quote])


    // Handle Submit
    const handleSubmit = async (e) => {
        e.preventDefault()

        const accessToken = localStorage.getItem('accessToken');
        if(accessToken) {
            try {
                const data = {...quote, price: viewPrice}
                const response = await axios.post(`${process.env.REACT_APP_SERVER_URL}quotes/`, data, {headers: { Authorization: `Bearer ${accessToken}` }});
                
                if (response.status === 201) {
                    navigate('/quotes')
                }
            } catch (error) {
                if (error.response && error.response.status === 401) {
                  await refreshAccessToken();
                }
                console.error(error);
            }
        }
    } 

    return (
        <Container maxWidth="lg" sx={{position: 'relative'}}>
            {viewPrice > 0 && <Chip icon={<AttachMoneyRoundedIcon />} label={viewPrice} color='primary' sx={{position: 'fixed', bottom: 10, right: 20, fontWeight: 'bold'}} />}
            <Typography variant="h2" gutterBottom sx={{textAlign: {xs: 'center', md: 'left'}}}>Create Quote</Typography>
            <Stack gap={5} component='form' onSubmit={handleSubmit}>
                {/* Info */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><InfoRoundedIcon sx={{mr: 1}} /><span>Info</span></div></Typography>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={6}><TextField id="business-name" label="Business Name" variant="outlined" required fullWidth value={quote.business_name} onChange={(e) => {setQuote({...quote, business_name: e.target.value})}} /></Grid>
                        <Grid item xs={12} md={6}><TextField id="contact-name" label="Contact Name" variant="outlined" required fullWidth value={quote.contact_name} onChange={(e) => {setQuote({...quote, contact_name: e.target.value})}} /></Grid>
                        <Grid item xs={12}><TextField id="notes" label="Additional Notes" variant="outlined" fullWidth multiline rows={4} value={quote.notes} onChange={(e) => {setQuote({...quote, notes: e.target.value})}} /></Grid>
                    </Grid>
                </Paper>

                {/* Plating */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom sx={{pb:4}}><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><OilBarrelRoundedIcon sx={{mr: 1}} /><span>Plating</span></div></Typography>
                    <Grid container spacing={8}>
                        <Grid item xs={12} md={4} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl>
                                <FormLabel id="demo-row-radio-buttons-group-label">Process Type</FormLabel>
                                <RadioGroup
                                    row
                                    aria-labelledby="demo-row-radio-buttons-group-label"
                                    name="row-radio-buttons-group"
                                    value={quote.brazed ? 'brazed' : 'electroplated'}
                                    onChange={(e) => {setQuote({...quote, brazed: e.target.value === "brazed"})}}
                                >
                                    <FormControlLabel value="electroplated" control={<Radio />} label="Electroplated" />
                                    <FormControlLabel value="brazed" control={<Radio />} label="Brazed" />
                                </RadioGroup>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} md={4} sx={{display: 'flex', justifyContent: 'center'}}>
                            <TextField
                                id="surface-area"
                                label="Surface Area (sq. inches)"
                                value={quote.surface_area}
                                variant="outlined"
                                sx={{maxWidth: '300px'}}
                                type='number'
                                inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]*$', min: 0, step: "0.001" }}
                                onChange={(e) => {setQuote({...quote, surface_area: e.target.value})}}
                            />
                        </Grid>
                        <Grid item xs={12} md={4} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl>
                                <FormLabel id="electroless-radio-buttons-group-label">Electroless Needed?</FormLabel>
                                <FormControlLabel
                                value="electroless"
                                control={<Switch color="primary" />}
                                sx={{pl: 6}}
                                onChange={(e) => {setQuote({...quote, electroless: !quote.electroless})}}
                            />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Paper>

                {/* Particles */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom sx={{pb:4}}><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><DiamondRoundedIcon sx={{mr: 1}} /><span>Particles</span></div></Typography>
                    <Grid container spacing={8}>
                        <Grid item xs={12} sm={6} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl>
                                <FormLabel id="particle-type">Particle Type</FormLabel>
                                <RadioGroup row aria-labelledby="particle options" name="particle-options" value={quote.particle} onChange={(e) => {setQuote({...quote, particle: e.target.value})}}>
                                    <FormControlLabel value="synthetic" control={<Radio />} label="Synthetic" />
                                    <FormControlLabel value="natural" control={<Radio />} label="Natural" />
                                    <FormControlLabel value="blend" control={<Radio />} label="Blend" />
                                    <FormControlLabel value="cbn" control={<Radio />} label="CBN" />
                                </RadioGroup>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl sx={{width: '200px'}}>
                                <InputLabel id="grit-size">Grit Size</InputLabel>
                                <Select
                                labelId="grit-sizes"
                                id="grit-sizes"
                                label="Grit Size"
                                value={quote.grit}
                                onChange={(e) => {setQuote({...quote, grit: e.target.value})}}
                                >
                                    <MenuItem value='16/18'>16/18</MenuItem>
                                    <MenuItem value='18/20'>18/20</MenuItem>
                                    <MenuItem value='20/30'>20/30</MenuItem>
                                    <MenuItem value='30/40'>30/40</MenuItem>
                                    <MenuItem value='40/50'>40/50</MenuItem>
                                    <MenuItem value='50/60'>50/60</MenuItem>
                                    <MenuItem value='60/80'>60/80</MenuItem>
                                    <MenuItem value='80/100'>80/100</MenuItem>
                                    <MenuItem value='80/120'>80/120</MenuItem>
                                    <MenuItem value='100/120'>100/120</MenuItem>
                                    <MenuItem value='120/140'>120/140</MenuItem>
                                    <MenuItem value='140/170'>140/170</MenuItem>
                                    <MenuItem value='170/200'>170/200</MenuItem>
                                    <MenuItem value='200/230'>200/230</MenuItem>
                                    <MenuItem value='230/270'>230/270</MenuItem>
                                    <MenuItem value='270/325'>270/325</MenuItem>
                                    <MenuItem value='325/400'>325/400</MenuItem>
                                    <MenuItem value='400'>400</MenuItem>
                                    <MenuItem value='500'>500</MenuItem>
                                    <MenuItem value='600'>600</MenuItem>
                                    <MenuItem value='800'>800</MenuItem>
                                    <MenuItem value='1200'>1200</MenuItem>
                                    <MenuItem value='1500'>1500</MenuItem>
                                    <MenuItem value='2000'>2000</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>
                </Paper>

                {/* Fixture */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom sx={{pb:4}}><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><EngineeringRoundedIcon sx={{mr: 1}} /><span>Fixture</span></div></Typography>
                    <Grid container justifyContent="center" spacing={8}>
                        <Grid item xs={12} sm={6} md={4} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl>
                                    <FormLabel id="demo-row-radio-buttons-group-label">Fixture Needed?</FormLabel>
                                    <FormControlLabel
                                    control={<Switch color="primary" />}
                                    sx={{pl: 6}}
                                    value={quote.fixture}
                                    onChange={(e) => setQuote({...quote, fixture: !quote.fixture, fixture_cost: 0})}
                                />
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4} sx={{display: 'flex', justifyContent: 'center'}}>
                            {quote.fixture &&
                            <FormControl sx={{ m: 1 }}>
                                <InputLabel htmlFor="outlined-adornment-amount">Estimated Cost of Fixture</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-amount"
                                    startAdornment={<InputAdornment position="start">$</InputAdornment>}
                                    label="Estimated Cost of Fixture"
                                    type='number'
                                    value={quote.fixture_cost}
                                    inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]{0,2}$', min: 0 }}
                                    onChange={(e) => setQuote({...quote, fixture_cost: e.target.value})}
                                />
                            </FormControl>
                            }
                        </Grid>
                    </Grid>
                </Paper>

                {/* Tooling */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom sx={{pb:4}}><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><ConstructionRoundedIcon sx={{mr: 1}} /><span>Tooling</span></div></Typography>
                    <Grid container spacing={8}>
                        <Grid item xs={12} >
                            <FormControl>
                                <FormLabel id="tooling-type">Tooling Type</FormLabel>
                                <RadioGroup row aria-labelledby="tool options" name="tool-options" value={quote.tooling_type} onChange={(e) => setQuote({...quote, tooling_type: e.target.value, stripReplate: "small/easy part", blankCost: 0, machining: false, machineCost: 0})} >
                                    <FormControlLabel value="Customer Provided" control={<Radio />} label="Customer Provided" />
                                    <FormControlLabel value="Strip & Replate" control={<Radio />} label="Strip & Replate" />
                                    <FormControlLabel value="Blank Needed" control={<Radio />} label="Blank Needed" />
                                </RadioGroup>
                            </FormControl>
                        </Grid>
                        {quote.tooling_type === 'Strip & Replate' &&
                            <Grid item xs={12}>
                                <FormControl>
                                    <FormLabel id="strip-adjustment">Adjustment for Strip/Replate</FormLabel>
                                    <RadioGroup row aria-labelledby="strip options" name="strip-options" value={quote.strip_replate} onChange={(e) => setQuote({...quote, strip_replate: e.target.value})}>
                                        <FormControlLabel value="small/easy part" control={<Radio />} label="Small/Easy Part" />
                                        <FormControlLabel value="medium size/difficulty" control={<Radio />} label="Medium Size/Difficulty" />
                                        <FormControlLabel value="large/difficult part" control={<Radio />} label="Large/Difficult Part" />
                                    </RadioGroup>
                                </FormControl>
                            </Grid>
                        }
                        {quote.tooling_type === 'Blank Needed' && 
                        <>
                            <Grid item xs={12} sm={6} md={4}>
                                <FormControl sx={{ m: 1 }}>
                                    <InputLabel htmlFor="estimated-blank-cost">Estimated Cost for a Blank</InputLabel>
                                    <OutlinedInput
                                        id="estimated-blank"
                                        startAdornment={<InputAdornment position="start">$</InputAdornment>}
                                        label="Estimated Cost for a Blank"
                                        type='number'
                                        value={quote.blank_cost}
                                        inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]{0,2}$', min: 0 }}
                                        onChange={(e) => setQuote({...quote, blank_cost: e.target.value})}
                                    />
                                </FormControl>
                            </Grid>
                            <Grid item xs={12} sm={6} md={4}>
                                <FormControl>
                                        <FormLabel id="machining-needed-label">Additional Machining Needed?</FormLabel>
                                        <FormControlLabel
                                        control={<Switch color="primary" />}
                                        sx={{pl: 10}}
                                        value={quote.machining}
                                        onChange={() => setQuote({...quote, machining: !quote.machining, machineCost: 0})}
                                    />
                                </FormControl>
                            </Grid>
                            {quote.machining && 
                                <Grid item xs={12} sm={6} md={4}>
                                    <FormControl sx={{ m: 1 }}>
                                        <InputLabel htmlFor="estimated-machine-cost">Estimated Cost for Machining</InputLabel>
                                        <OutlinedInput
                                            id="estimated-machining"
                                            startAdornment={<InputAdornment position="start">$</InputAdornment>}
                                            label="Estimated Cost for Machining"
                                            type='number'
                                            value={quote.machine_cost}
                                            inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]{0,2}$', min: 0 }}
                                            onChange={(e) => setQuote({...quote, machine_cost: e.target.value})}
                                        />
                                    </FormControl>
                                </Grid>
                            }
                        </>
                        }
                    </Grid>
                </Paper>

                {/* Labor */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom sx={{pb:4}}><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><GroupsRoundedIcon sx={{mr: 1}} /><span>Labor</span></div></Typography>
                    <Grid container justifyContent="center" spacing={8}>
                        <Grid item xs={12} sm={6} lg={3} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="prep-time">Prep Time</InputLabel>
                                <OutlinedInput
                                    id="prep-time"
                                    endAdornment={<InputAdornment position="end">hrs.</InputAdornment>}
                                    label="Prep Time"
                                    type='number'
                                    value={quote.prep_labor}
                                    inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]*$', min: 0, step: "0.001" }}
                                    onChange={(e) => setQuote({...quote, prep_labor: e.target.value})}
                                    />
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6} lg={3} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="plating-time">Plating Time</InputLabel>
                                <OutlinedInput
                                    id="plating-time"
                                    endAdornment={<InputAdornment position="end">hrs.</InputAdornment>}
                                    label="Plating Time"
                                    type='number'
                                    value={quote.plating_labor}
                                    inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]*$', min: 0, step: "0.001" }}
                                    onChange={(e) => setQuote({...quote, plating_labor: e.target.value})}
                                    />
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6} lg={3} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="clean-time">Cleanup Time</InputLabel>
                                <OutlinedInput
                                    id="cleanup-time"
                                    endAdornment={<InputAdornment position="end">hrs.</InputAdornment>}
                                    label="Cleanup Time"
                                    type='number'
                                    value={quote.cleaning_labor}
                                    inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]*$', min: 0, step: "0.001" }}
                                    onChange={(e) => setQuote({...quote, cleaning_labor: e.target.value})}
                                    />
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6} lg={3} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl fullWidth sx={{ m: 1 }}>
                                <InputLabel htmlFor="pack-time">QC/Pack Time</InputLabel>
                                <OutlinedInput
                                    id="pack-time"
                                    endAdornment={<InputAdornment position="end">hrs.</InputAdornment>}
                                    label="QC/Pack Time"
                                    type='number'
                                    value={quote.pack_labor}
                                    inputProps={{ inputMode: 'numeric', pattern: '^[0-9]*.?[0-9]*$', min: 0, step: "0.001" }}
                                    onChange={(e) => setQuote({...quote, pack_labor: e.target.value})}
                                    />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Paper>

                {/* Project */}
                <Paper elevation={2} sx={{p: 2}}>
                    <Typography variant="h5" gutterBottom sx={{pb:4}}><div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}><AccountTreeRoundedIcon sx={{mr: 1}} /><span>Project</span></div></Typography>
                    <Grid container justifyContent="center" spacing={8}>
                        <Grid item xs={12} lg={6} sx={{display: 'flex', justifyContent: 'center'}}>
                        <FormControl>
                                <FormLabel id="difficulty-adjustment">Part Difficulty</FormLabel>
                                <RadioGroup row aria-labelledby="strip options" name="difficulty-options" value={quote.difficulty} onChange={(e) => setQuote({...quote, difficulty: e.target.value})}>
                                    <FormControlLabel value="easy" control={<Radio />} label="Easy" />
                                    <FormControlLabel value="moreDifficult" control={<Radio />} label="More Difficult" />
                                    <FormControlLabel value="veryDifficult" control={<Radio />} label="Very Difficult" />
                                </RadioGroup>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} lg={6} sx={{display: 'flex', justifyContent: 'center'}}>
                            <FormControl>
                                    <FormLabel id="expediting-needed-label">Need to be Expedited?</FormLabel>
                                    <FormControlLabel
                                    control={<Switch color="primary" />}
                                    sx={{pl: 7}}
                                    value={quote.expedited}
                                    onChange={() => setQuote({...quote, expedited: !quote.expedited})}
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Paper>

                <Button type='submit' variant='contained' size='large' startIcon={<SaveAsRoundedIcon />} sx={{py:2}}>Create Quote</Button>
            </Stack>
        </Container>
    )
}

export default CreateQuote